.background {
  background-image: url("background.png");
  background-size: cover; /* Dopasowanie obrazka do rozmiaru kontenera */
  background-repeat: no-repeat; /* Zapobieganie powtarzaniu obrazka */
  background-position: center; /* Wyśrodkowanie obrazka */
  display: flex;
  
  top: 0px;
  left: 0px;
  width: 100%;
  height: 600px;
  z-index: -1; /* Ustawienie tła za innymi elementami */
  box-sizing: border-box;
  justify-content: space-between;
}


.logo {
  max-width: 400px;
  max-height: 200px;
  margin-top: 20px;
  margin-left: 40px;
}

.button-lang {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5px;
}

@media (max-width: 768px) {
  .background {
    height: 400px;
  }

  .logo {
    max-width: 500px;
    margin-top: 70px;
    margin-left: 0px;
    max-height: 100px;
  }
}

@media (max-width: 480px) {
  .background {
    height: 400px;
  }

  .logo {
    max-width: 400px;
    margin-top: 70px;
    margin-left: 0px;
  }
}